<template>
  <div class="movie-detail">
    <h2> {{ movie.Title }} </h2>
    <p> Year of release : {{ movie.Year }}</p>
    <div class="movie-plot">
      <img :src="movie.Poster" alt="" class="featured-img" />
      <div>
        <p>{{ movie.Plot }}</p>

        <p><b> IMDB Ratings</b> :{{ movie.Ratings[0].Value }}</p>
        <p><b> Revenue Generated</b> :{{ movie.BoxOffice }}</p>
      </div>
    </div>
  </div>
</template>
<script>
import { ref, onBeforeMount } from "vue";
import { useRoute } from "vue-router";
import enf from "@/enf.js";
export default {
  setup() {
    const movie = ref({});
    const route = useRoute();
    onBeforeMount(() => {
      fetch(
        `http://www.omdbapi.com/?apikey=${enf.apikey}&i=${route.params.id}&plot=full`
      )
        .then(res => res.json())
        .then(data => {
          movie.value = data;
        });
    });
    return {
      movie,
    };
  },
};
</script>
<style lang="scss">
.movie-detail {
  padding: 16px;
  h2 {
    color: white;
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 16px;
  }
  p {
    color: white;
  }
  .movie-plot {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .featured-img {
      display: block;
      max-width: 200px;
      margin-bottom: 15px;
    }
    p {
      color: white;
      margin: 0 10px;
    }
  }
  @media (max-width: 550px) {
    .movie-plot {
      flex-direction: column;
    }
  }
}
</style>
