<template>
  <div class="home">
    <div class="feature_card">
      <img
        src="https://graphicriver.img.customer.envatousercontent.com/files/264377858/eps20190502170373220190502cinemafilm02.jpg?auto=compress%2Cformat&q=80&fit=crop&crop=top&max-h=8000&max-w=590&s=a11309d118ae2773fed3a504d2b2c2ea"
        class="featured-image"
      />
      <div class="details">
        <h3>One stop solution for all movies</h3>
        <p
          >A Vue js build which helps you to find all the information about your
          favourite movies</p
        >
      </div>
    </div>
    <form @submit.prevent="submit()" class="search-box">
      <input
        type="text"
        placeholder="What are you looking for"
        v-model="search"
      />
      <input type="submit" value="search" />
    </form>
    <div class="movies-list">
      <div class="movie" v-for="movie in movies" v-bind:key="movie.imdbID">
        <!-- {{ movie.Title }} -->
        <router-link :to="'/movie/' + movie.imdbID" class="movie-link">
          <div class="product-image">
            <img :src="movie.Poster" alt="" />
            <div class="type"> {{ movie.Type }}</div>
          </div>
          <div class="detail">
            <p class="year">{{ movie.Year }}</p>
            <h3>{{ movie.Title }}</h3>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import enf from "@/enf.js";
export default {
  setup() {
    const search = ref("");
    const movies = ref([]);
    const submit = () => {
      if (search.value != "") {
        // console.log(search.value);
        fetch(`http://www.omdbapi.com/?apikey=${enf.apikey}&s=${search.value}`)
          .then(res => res.json())
          .then(data => {
            // console.log(data);
            movies.value = data.Search;
            console.log(movies.value);
            search.value = "";
          });
      }
    };
    return {
      search,
      movies,
      submit,
    };
  },
  name: "Home",
};
</script>
